.dpd-parcelshop-shipping-method-content {
	width: 100%;
	padding: 5px 0;

	&__open-btn-wrap {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-pack: start;
		-ms-flex-pack: start;
		justify-content: flex-start;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
	}

	&__logo {
		width: 80px;
		margin-right: 15px;
	}

	&__open-btn {
		color: #fff;
		background-color: #dc0032;
		border: 0;
		-webkit-box-shadow: none;
		box-shadow: none;
		outline: none;
		padding: 5px 15px;
		cursor: pointer;
		font-size: 14px;
		height: auto;
		line-height: 1.2;
	}

	&__chosen-parcelshop-wrap {
		width: 100%;
		padding: 5px 0;
		display: none;
	}

	&__chosen-parcelshop-wrap.active {
		display: block;
	}

	&__chosen-parcelshop {
		margin-bottom: 0;
	}
}
